const { Component } = wp.element;

export default class Feed extends Component {
  constructor(props) {
      super(...arguments);
      this.props = props;
      this.results = [];
      if ( this.props.results ) {
        this.results = this.props.results;
      }
  }

  render() {
      return (
          <div>
          {
            this.results.map(function(item, i){
              return (
                <div className="randomItem" key={i}>
                  { item.thumbnail !== "self" && item.thumbnail !== "" ? ( <img src={item.thumbnail} /> ) : ( <img src="data:image/png;base64,"/> ) }
                  <a href={item.url} target="_blank"><h4>{item.title}</h4></a>
                </div>
              )
            })
          }
          </div>
      );
  }

}
  